import Head from 'next/head';

export default function PageHead({ currentPage }) {
  let pageTitle = 'MapaDengue - Início';

  switch (currentPage) {
    case 'Mapa':
      pageTitle = 'MapaDengue - Mapa';
      break;
    case 'Relatorios':
      pageTitle = 'MapaDengue - Relatórios';
      break;
    case 'Sobre':
      pageTitle = 'MapaDengue - Sobre';
      break;
    default:
      break;
  }

  return (
    <Head>
      <meta charSet="UTF-8" />
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
      <title>{pageTitle}</title>
      <link 
        href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css" 
        rel="stylesheet" 
      />
      <link 
        rel="stylesheet" 
        href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css" 
      />
    </Head>
  );
}